import React from 'react';
import { Link, animateScroll as scroll } from 'react-scroll';
import './Navigation.css';

export default function ScrollableTabsButtonForce() {
	return (
		<div>
			<Link to='about-me' spy={true} smooth={true} offset={-70} duration={500}>
				About Me
			</Link>
			<Link to='project' spy={true} smooth={true} offset={-70} duration={500}>
				Project
			</Link>
			<Link to='experience' spy={true} smooth={true} offset={-70} duration={500}>
				Experience
			</Link>
			<Link to='skill' spy={true} smooth={true} offset={-70} duration={500}>
				Skill
			</Link>
			<Link to='contact-me' spy={true} smooth={true} offset={-70} duration={500}>
				contact me
			</Link>
		</div>
	);
}
